import React, { useRef } from 'react'
import { useSprings, animated } from '@react-spring/web'
import useMeasure from 'react-use-measure'
import { useDrag } from 'react-use-gesture'
import clamp from 'lodash.clamp'

const pages = [
  '/taichi.jpg',
  '/greece1.jpg',
  '/greece2.jpg',
  '/london.jpg',
  '/morocco1.jpg',
  '/morocco2.jpg',
  '/paris.jpg',
  '/turkey.jpg',
]

function Viewpager() {
  const index = useRef(0)
  const [ref, { width }] = useMeasure()
  const [props, api] = useSprings(
    pages.length,
    i => ({
      x: i * width,
      scale: width === 0 ? 0 : 1,
      display: 'block',
    }),
    [width]
  )
  const bind = useDrag(({ active, movement: [mx], direction: [xDir], distance, cancel }) => {
    if (active && distance > width / 2) {
      index.current = clamp(index.current + (xDir > 0 ? -1 : 1), 0, pages.length - 1)
      cancel()
    }
    api.start(i => {
      if (i < index.current - 1 || i > index.current + 1) return { display: 'none' }
      const x = (i - index.current) * width + (active ? mx : 0)
      const scale = active ? 1 - distance / width / 2 : 1
      return { x, scale, display: 'block' }
    })
  })
  return (
    <div ref={ref} className={'wrapper'}>
      {props.map(({ x, display, scale }, i: number) => (
        <animated.div className={'page'} {...bind()} key={i} style={{ display, x }}>
          <animated.div style={{ scale, backgroundImage: `url(${pages[i]})` }} />
          {
            i === 0 && (
              <div className={'text-wrapper'}>
                <p className={'text'}>Hello there, my name is TaiChi. Web and blockchain developer, meanwhile a worldwide traveler. Have been traveled to 20+ counties 40+ cities.</p>
              </div>
            )
          }

          {
            i === props.length - 1 && (
              <div className={'text-wrapper'}>
                <p className={'text'}>"Life is either a daring adventure or nothing at all" Helen Keller.</p>
              </div>
            )
          }
        </animated.div>
      ))}
    </div>
  )
}

export default function App() {
  return (
    <div className={'container'}>
      <Viewpager />
    </div>
  )
}
